.continents {
    background-color: #161616;
    color: #e5f5f5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    position: relative;
    width:250px;
}

.continents h1{
    background-color: #161616;
    padding: 10px;
}

.continents-list{
    list-style: none;
    background-color: #151719;
    width: 100%;
    height: 100%;
    left:0;
    padding: 0px;
}

.continents-list li {
    background-color: #161616;
    font-color: white;
    padding: 15px 10px;
    border-top: solid 1px #f5f5f5;
}

.continents-list li:last-child{
    border-bottom: solid 1px #f5f5f5;
}

.continents-list a{
    font-family: arial, sans-serif;
    color:#f5f5f5;
    background-color: #151719;
}

